<template>
  <div class="pdf-merge">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>PDF Merge</span>
          <el-icon><DocumentCopy /></el-icon>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="Select PDF Files" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="10"
            :on-change="handleFileChange"
            :on-remove="handleFileRemove"
            :before-upload="beforeUpload"
            accept=".pdf"
            multiple
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              Drag PDF files here, or <em>click to upload</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                You can upload multiple PDF files, max 100MB per file
              </div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="Merge Order">
          <el-input
            v-model="form.mergeOrder"
            placeholder="Merge order, default is upload order"
            clearable
          />
          <div class="form-tip">
            You can specify file merge order, leave empty to merge by upload order
          </div>
        </el-form-item>

        <el-form-item label="Output File Name">
          <el-input
            v-model="form.outputFileName"
            placeholder="Output file name, default is merged.pdf"
            clearable
          />
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            :loading="loading"
            :disabled="selectedFiles.length < 2"
            @click="handleSubmit"
          >
            {{ loading ? 'Processing...' : 'Start Merge' }}
          </el-button>
          <el-button @click="resetForm">Reset</el-button>
        </el-form-item>
      </el-form>

      <!-- File List -->
      <div v-if="selectedFiles.length > 0" class="file-list">
        <el-divider>Selected Files</el-divider>
        <el-table :data="selectedFiles" style="width: 100%">
          <el-table-column prop="name" label="File Name" />
          <el-table-column prop="size" label="File Size" width="120" />
          <el-table-column label="Action" width="100">
            <template #default="scope">
              <el-button
                type="danger"
                size="small"
                @click="removeFile(scope.$index)"
              >
                Remove
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- Results Display -->
      <div v-if="result" class="result-section">
        <el-divider>Processing Results</el-divider>
        <el-alert
          :title="result.msg"
          type="success"
          :closable="false"
          show-icon
        />
        <div class="result-info">
          <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>Merged File:</strong> {{ result.data.mergedFile }}</p>
          <p><strong>File Size:</strong> {{ result.data.fileSize }}</p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            Download Result
          </el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { DocumentCopy, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { downloadFile } from '@/utils/download'
import { validateFile, formatFileSize } from '@/utils/file'

const uploadRef = ref()
const loading = ref(false)
const selectedFiles = ref([])
const result = ref(null)

const form = reactive({
  mergeOrder: '',
  outputFileName: 'merged.pdf'
})

// File selection handling
const handleFileChange = (file, fileList) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  
  selectedFiles.value = fileList.map(item => ({
    name: item.name,
    size: formatFileSize(item.size),
    raw: item.raw
  }))
}

// File removal handling
const handleFileRemove = (file, fileList) => {
  selectedFiles.value = fileList.map(item => ({
    name: item.name,
    size: formatFileSize(item.size),
    raw: item.raw
  }))
}

// Remove file
const removeFile = (index) => {
  selectedFiles.value.splice(index, 1)
  uploadRef.value.clearFiles()
  selectedFiles.value.forEach(file => {
    uploadRef.value.handleStart(file.raw)
  })
}

// Pre-upload validation
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // Prevent auto-upload
}

// Submit processing
const handleSubmit = async () => {
  if (selectedFiles.value.length < 2) {
    ElMessage.warning('At least 2 PDF files are required for merging')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    
    // Add all files
    selectedFiles.value.forEach(file => {
      formData.append('files', file.raw)
    })
    
    formData.append('mergeOrder', form.mergeOrder)
    formData.append('outputFileName', form.outputFileName)

    const response = await pdfApi.mergePdfs(formData)
    result.value = response
    ElMessage.success('PDF merge successful')
  } catch (error) {
    console.error('PDF merge failed:', error)
  } finally {
    loading.value = false
  }
}

// Download result
const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = result.value.data.mergedFile || 'merged.pdf'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

// Reset form
const resetForm = () => {
  form.mergeOrder = ''
  form.outputFileName = 'merged.pdf'
  selectedFiles.value = []
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-merge {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.file-list {
  margin-top: 20px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
}
</style>
